@use 'base/variables' as v;
@use 'base/mixins' as m;


.no-proyectos {
    text-align: center;
    a {
        color: v.$indigo;
    }
}

.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid(1, 2rem);
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
    @include m.desktop {
        @include m.grid(3, 2rem);
    }

    .eliminar-proyecto {
        border: none;
        font-size: 2rem;
        padding: .5rem 1rem;
        text-transform: uppercase;
        font-weight: 700;
        border-radius: 1rem;

        &:hover {
            cursor: pointer;
        }
        background-color: v.$rojo;
        color: v.$blanco;
        &:hover {
            color: darken(v.$rojo, 20%);
        }
    }

    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: .5rem;
        justify-content: space-between;

        a {
            &:hover {
                color: v.$naranja;
            }
        }

        &:hover {
            background-color: darken(v.$indigo, 5%);
        }
    }

    a {
        color: v.$blanco;
    }
}